
'use client';
import * as React from 'react';
import Image from 'next/image';
import Link from 'next/link';
import {
  Carousel,
  CarouselContent,
  CarouselItem,
  CarouselNext,
  CarouselPrevious,
} from '@/components/ui/carousel';
import { Card, CardContent, CardHeader, CardTitle } from '@/components/ui/card';
import { Skeleton } from '@/components/ui/skeleton';
import Autoplay from 'embla-carousel-autoplay';

interface BlogPost {
  title: string;
  link: string;
  thumbnail: string;
  description: string;
}

export default function BlogSection() {
    const [posts, setPosts] = React.useState<BlogPost[]>([]);
    const [loading, setLoading] = React.useState(true);

    const plugin = React.useRef(
        Autoplay({ delay: 5000, stopOnInteraction: true })
    );

    React.useEffect(() => {
        const fetchPosts = async () => {
            setLoading(true);
            try {
                const response = await fetch('/api/blog');
                const data = await response.json();
                if (data.posts) {
                    setPosts(data.posts);
                }
            } catch (error) {
                console.error('Error fetching blog posts:', error);
            } finally {
                setLoading(false);
            }
        };

        fetchPosts();
    }, []);

    if (loading) {
        return (
            <section id="blog" className="py-20 sm:py-32 bg-black">
                <div className="container">
                     <div className="text-center">
                        <h2 className="font-headline text-5xl font-bold uppercase text-primary md:text-6xl">
                            Blog
                        </h2>
                        <p className="mt-4 max-w-3xl mx-auto text-muted-foreground text-xl">
                            Cargando últimas entradas...
                        </p>
                    </div>
                    <div className="mt-12 grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-8">
                        {[...Array(3)].map((_, index) => (
                             <div key={index} className="p-2 md:p-4">
                                <Card className="overflow-hidden bg-background">
                                    <CardHeader className="p-0">
                                        <Skeleton className="h-48 w-full" />
                                    </CardHeader>
                                    <CardContent className="p-6">
                                        <Skeleton className="h-6 w-3/4 mb-4" />
                                        <Skeleton className="h-4 w-full" />
                                         <Skeleton className="h-4 w-5/6 mt-2" />
                                    </CardContent>
                                </Card>
                            </div>
                        ))}
                    </div>
                </div>
            </section>
        );
    }

    if (posts.length === 0) {
        return null; // Don't render anything if there are no posts after loading
    }

  return (
    <section id="blog" className="py-20 sm:py-32 bg-black">
      <div className="container">
        <div className="text-center">
          <h2 className="font-headline text-5xl font-bold uppercase text-primary md:text-6xl">
            Blog
          </h2>
          <p className="mt-4 max-w-3xl mx-auto text-muted-foreground text-xl">
            Consejos de expertos, novedades y todo lo que necesitas saber sobre el cuidado automotriz.
          </p>
        </div>
        <Carousel 
            plugins={[plugin.current]}
            onMouseEnter={plugin.current.stop}
            onMouseLeave={plugin.current.reset}
            opts={{
                align: 'start',
                loop: true,
            }}
            className="w-full mt-12"
        >
          <CarouselContent className="-ml-4">
            {posts.map((post, index) => (
              <CarouselItem key={index} className="pl-4 md:basis-1/2 lg:basis-1/3">
                <div className="h-full p-2 md:p-4">
                  <Link href={post.link} target="_blank" rel="noopener noreferrer" className="block h-full group">
                    <Card className="overflow-hidden bg-background h-full flex flex-col transition-transform duration-300 group-hover:scale-[1.03]">
                        <CardHeader className="p-0">
                           <div className="relative h-48 w-full overflow-hidden">
                               {post.thumbnail ? (
                                    <Image
                                        src={post.thumbnail}
                                        alt={post.title}
                                        fill
                                        className="object-cover transition-transform duration-300 group-hover:scale-105"
                                    />
                               ) : (
                                    <div className="w-full h-full bg-secondary flex items-center justify-center">
                                        <span className="text-muted-foreground text-sm">Sin imagen</span>
                                    </div>
                               )}
                           </div>
                        </CardHeader>
                        <CardContent className="p-6 flex flex-col flex-grow">
                          <CardTitle className="font-body text-2xl font-bold text-white mb-2 h-24 line-clamp-3">
                            {post.title}
                          </CardTitle>
                          <p className="text-muted-foreground text-base line-clamp-3">
                            {post.description}
                          </p>
                        </CardContent>
                    </Card>
                  </Link>
                </div>
              </CarouselItem>
            ))}
          </CarouselContent>
          <CarouselPrevious className="text-white hover:text-primary" />
          <CarouselNext className="text-white hover:text-primary" />
        </Carousel>
      </div>
    </section>
  );
}
